import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';

import { ENV } from '@app/config';
import * as fromRoot from '../../../../app/reducers';
import * as fromAuth from '../../../../modules/auth/reducers/auth.reducer';
import * as sharedActions from './../../../shared/actions/shared.actions';
import { User } from '../../../user/models/user.model';

@IonicPage({
  name: 'landing-page',
  segment: 'landing-page'
})
@Component({
  selector: 'page-landing',
  templateUrl: 'landing.page.html',
})
export class LandingPageComponent implements OnInit {

  public ENV = ENV;

  public user$: Observable<User>

  public constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private store: Store<{}>
  ) { }

  public ngOnInit() {
    this.user$ = this.store.select(fromAuth.getCurrentAuthUser);
  }

  public ionViewDidLoad() { }

  public setupInitialDatabase() {
    this.store.dispatch(new sharedActions.SetupInitialDatabase);
  }

}



